<template>
  <v-container
    id="regular-forms"
    fluid
    tag="section"
  >
    <v-col cols="12">
      <base-material-card
        color="primary"
        icon="mdi-text"
        :title="this.$t('nav.point_of_service')"
        class="px-5 py-3"
      >
        <v-form
          ref="form"
          lazy-validation
        >
          <v-col>
            <vue-editor
              v-model="api.privacy_policy"
              required
            />
          </v-col>

          <v-col>
            <v-spacer />
            <v-btn
              color="primary"
              @click="submit"
            >
              Update
            </v-btn>
          </v-col>
        </v-form>
      </base-material-card>
    </v-col>
  </v-container>
</template>

<script>
  import { VueEditor } from 'vue2-editor'

  export default {
    name: 'Edit',
    components: {
      VueEditor,
    },
    data: () => ({
      api: {
        privacy_policy: '',
      },
    }),
    mounted: function () {
      this.$http.get(`/portal/user/organization`)
        .then(
          (response) => {
            this.api = response.data
          }
        )
    },
    methods: {
      submit () {
        if (!this.$refs.form.validate()) {
          return
        }

        this.$http.post(
          `/portal/user/organization/privacy_policy`,
          JSON.stringify({
            privacy_policy: this.api.privacy_policy,
          })
        )
          .then((response) => {
            this.$toasted.global.success(response.data.message)
          })
          .catch((error) => {
            this.$toasted.global.error(error.response.data.message)
          })
      },
    },
  }
</script>
